import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { Layout, Breadcrumb, Button, Table, Popconfirm, message } from 'antd';
const { Content } = Layout;

export class List extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            columns: [{
                title: '登录名',
                dataIndex: 'loginName'
            }, {
                title: '姓名',
                dataIndex: 'name'
            }, {
                title: '状态',
                dataIndex: 'status',
                render: status => status == "0" ? "未启用" : "启用"
            }, {
                title: '手机',
                dataIndex: 'mobile'
            }, {
                title: '修改时间',
                dataIndex: 'updateTime'
            }, {
                title: "操作",
                render: data => <div style={{"textAlign": "center"}}>
                    <Button type="primary" onClick={this.checkUserDetail.bind(this, data.key)} style={{"marginRight": "20px"}}>修改
                    </Button>
                    <Popconfirm placement="left" title="是否确认删除?" onConfirm={this.deleteUser.bind(this, data.key)}
                                okText="确认" cancelText="取消">
                        <Button type="danger" disabled={data.roleId == 1 ? true : false}>删除</Button>
                    </Popconfirm>
                </div>
            }],
            dataSource: [],
            pagination: {
                current: 1,
                total: 0
            },
            loading: false
        };
        ['changePage'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentWillMount() {
        this.getUserList(1);
    }

    getUserList(currentPage) {
        let that = this;
        this.setState({loading: true});
        ajax({
            url: "/auth/manage/getUserList",
            method: "post",
            data: {
                    current: currentPage ? currentPage : 1
            },
            callback: function(data) {
                that.setState(
                    {
                        loading: false,
                        pagination: {
                            current: currentPage,
                            total: data.data.total
                        },
                        dataSource: data.data.list
                    }
                )
            }
        });
    }

    addUser() {
        hashHistory.push("/auth/manage/add")
    }

    checkUserDetail(key) {
        hashHistory.push("/auth/manage/edit/" + key)
    }

    deleteUser(key) {
        let that = this;
        ajax({
            url: "/auth/manage/deleteUser",
            method: "post",
            data: {id: key},
            callback: function(data) {
                if(data.status == 200) {
                    message.success("删除成功", 1);
                    let newDataSource = that.state.dataSource.filter(item => { return item.key != key});
                    that.setState({dataSource: newDataSource});
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        });
    }

    changePage(pagination) {
        this.getUserList(pagination.current)
    }

    render() {
        return (
            <Layout style={{ padding: '0 24px 24px' }}>
                <Breadcrumb style={{ margin: '12px 0' }}>
                    {
                        this.props.routes.map(function(item, index) {
                            return <Breadcrumb.Item key="">{item.breadcrumbName}</Breadcrumb.Item>
                        })
                    }
                </Breadcrumb>
                <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>
                    <Button icon="plus" onClick={this.addUser} style={{"marginBottom": "20px"}}>添加用户</Button>
                    <Table
                        bordered
                        dataSource={this.state.dataSource}
                        columns={this.state.columns}
                        pagination={this.state.pagination}
                        onChange={this.changePage}
                        loading={this.state.loading}
                    />
                </Content>
            </Layout>
        )
    }
}
module.exports = List;


